.form-page {
  display: grid;
  grid-template-rows: 184px 48px auto 86px;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 5%;
  height: 95%;
}

.form-info-container {
  display: flex;
  justify-content: center;
  height: 48px;
}

.form-info-container > p {
  color: #0a7440;
  padding: 0px;
  margin: 0px;
  text-align: left;
  font-size: 12px;
  font-weight: bold;
}

.select-container,
.nav-button-container,
.form-info-container > p {
  width: 77%;
}

.nav-button-container {
  position: absolute;
  bottom: 0px;
  padding: 20px;
  justify-self: center;
  width: 77%;
  /* bottom: 5%; */
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  gap: 30px;
}

.nav-button-container > a {
  width: 100%;
}

.nav-button {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.form-header {
  display: flex;
  width: 77%;
  flex-direction: row;
  gap: 30px;
  align-items: center;
}

.page-contents {
  grid-row: 3/4;
  display: flex;
  width: 100%;
  /*TODO width: 100vw; */
  flex-direction: column;
  align-items: center;
  height: 100%;
  overflow-y: auto;
}

.form-header-container {
  align-self: self-end;
  display: flex;
  flex-direction: column;
  gap: 30px;
  align-items: center;
}

.form-title {
  color: #058f20;
  margin: 0px;
  padding: 0px;
  font-size: 24px;
  font-weight: bold;
}
